import html2pdf from "html2pdf.js";
import { type ExposureBand, type AnchorTimingBucket } from "@shared/schema";

const bandDefinitions: Record<ExposureBand, string> = {
  Low: "Low exposure indicates minimal reliance on manual journal orchestration during close.",
  Moderate: "Moderate exposure indicates operational reliance on manual journals with some established controls.",
  Elevated: "Elevated exposure indicates material reliance on manual journal processes with control gaps.",
  High: "High exposure indicates sustained reliance on manual journal orchestration creating material close risk.",
  Critical: "Critical exposure indicates severe reliance on manual journals creating substantial control and close risk."
};

export function generateResultsPDF(
  exposureBand: ExposureBand,
  exposureScore: number,
  summary: string,
  primaryDriver: string,
  driverInterpretation: string | undefined,
  anchorTimingBucket: AnchorTimingBucket,
  dependencyFlag: boolean,
  narrative: string
): void {
  const today = new Date();
  const dateGenerated = today.toLocaleDateString("en-US", {
    year: "numeric",
    month: "long",
    day: "numeric"
  });

  const htmlContent = `
    <div style="width: 100%; max-width: 800px; font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif; color: #0f172a; line-height: 1.6; margin: 0 auto; padding: 0; background-color: #ffffff;">
      
      <!-- Cover Section -->
      <div style="page-break-after: always; min-height: 1080px; padding: 64px 56px 56px 56px; border-bottom: 1px solid rgba(15,23,42,0.12); background: radial-gradient(circle at 78% 18%, rgba(19,170,176,0.12), rgba(255,255,255,0) 42%), linear-gradient(180deg, #f8fbff 0%, #eef4ff 100%); display: flex; flex-direction: column; justify-content: space-between;">
        <div style="text-align: center;">
          <p style="font-size: 12px; color: #64748b; text-transform: uppercase; letter-spacing: 0.22em; margin: 0 0 34px 0; font-weight: 600;">
            Journal Exposure Diagnostic Report
          </p>
          <h1 style="font-size: 64px; line-height: 1.1; font-weight: 700; margin: 0; color: #0f172a; letter-spacing: -0.02em;">
            Journal Automation Exposure Index
          </h1>
        </div>

        <div style="margin: 56px auto; width: 100%; max-width: 640px; border: 1px solid rgba(20,184,166,0.4); border-radius: 16px; background: linear-gradient(160deg, #ffffff, #f8fbff); box-shadow: 0 8px 28px rgba(15,23,42,0.08); padding: 42px 40px; text-align: center;">
          <p style="font-size: 12px; color: #64748b; text-transform: uppercase; letter-spacing: 0.18em; margin: 0 0 16px 0; font-weight: 600;">
            Exposure Band
          </p>
          <p style="font-size: 72px; line-height: 1.02; font-weight: 700; margin: 0 0 20px 0; color: #0f172a;">
            ${exposureBand}
          </p>
          <p style="font-size: 48px; font-weight: 600; margin: 0; color: #0f172a;">
            ${exposureScore}<span style="font-size: 34px; color: #475569;"> / 100</span>
          </p>
          <p style="font-size: 14px; color: #334155; max-width: 520px; margin: 26px auto 0 auto; line-height: 1.65;">
            Scores in this range typically indicate sustained reliance on manual orchestration rather than isolated exceptions.
          </p>
        </div>

        <div style="display: flex; justify-content: space-between; align-items: center; border-top: 1px solid rgba(15,23,42,0.12); padding-top: 18px;">
          <p style="font-size: 11px; color: #64748b; letter-spacing: 0.14em; text-transform: uppercase; margin: 0;">
            Assessment Results
          </p>
          <p style="font-size: 14px; color: #64748b; margin: 0;">
            Generated ${dateGenerated}
          </p>
        </div>
      </div>

      <!-- Exposure Summary Section -->
      <div style="page-break-after: auto; page-break-inside: avoid; padding: 64px 56px 46px 56px; background: #ffffff;">
        <h2 style="font-size: 16px; font-weight: 700; text-transform: uppercase; letter-spacing: 0.16em; color: #64748b; margin: 0 0 24px 0;">
          Exposure Summary
        </h2>
        <div style="background-color: #f8fafc; padding: 28px; border: 1px solid #dbe4f0; border-left: 5px solid #12d4cf; border-radius: 10px; margin-bottom: 24px;">
          <p style="font-size: 16px; color: #0f172a; margin: 0 0 14px 0;">
            <strong>Exposure Band:</strong> ${exposureBand}
          </p>
          <p style="font-size: 16px; color: #0f172a; margin: 0 0 14px 0;">
            <strong>Exposure Index Score:</strong> ${exposureScore} / 100
          </p>
          <p style="font-size: 16px; color: #334155; margin: 0;">
            At this exposure level, manual journal processes represent a structural source of close risk.
          </p>
        </div>
        <p style="font-size: 18px; color: #1e293b; line-height: 1.6; margin: 0 0 8px 0;">
          ${summary}
        </p>
      </div>

      <!-- Key Assessment Factors -->
      <div style="page-break-after: auto; padding: 42px 56px 46px 56px; background-color: #ffffff;">
        <h2 style="font-size: 16px; font-weight: 700; text-transform: uppercase; letter-spacing: 0.16em; color: #64748b; margin: 0 0 24px 0;">
          Key Assessment Factors
        </h2>
        
        <div style="margin-bottom: 18px; padding: 24px 22px; border: 1px solid #dbe4f0; border-radius: 10px; background-color: #f8fafc; page-break-inside: avoid;">
          <p style="font-size: 11px; text-transform: uppercase; letter-spacing: 0.2em; color: #64748b; margin: 0 0 12px 0; font-weight: 700;">
            Primary Contributing Factor
          </p>
          <p style="font-size: 44px; font-weight: 700; color: #0f172a; margin: 0 0 12px 0; line-height: 1.08;">
            ${primaryDriver}
          </p>
          ${driverInterpretation ? `<p style="font-size: 15px; color: #334155; margin: 0; line-height: 1.6;">${driverInterpretation}</p>` : ""}
        </div>

        <div style="margin-bottom: 18px; padding: 24px 22px; border: 1px solid #dbe4f0; border-radius: 10px; background-color: #f8fafc; page-break-inside: avoid;">
          <p style="font-size: 11px; text-transform: uppercase; letter-spacing: 0.2em; color: #64748b; margin: 0 0 12px 0; font-weight: 700;">
            Journal Timing
          </p>
          <p style="font-size: 44px; font-weight: 700; color: #0f172a; margin: 0; line-height: 1.08;">
            ${anchorTimingBucket.charAt(0).toUpperCase() + anchorTimingBucket.slice(1)}-cycle finalization
          </p>
        </div>

        ${dependencyFlag ? `
        <div style="padding: 24px 22px; border: 1px solid #dbe4f0; border-radius: 10px; background-color: #f8fafc; page-break-inside: avoid;">
          <p style="font-size: 11px; text-transform: uppercase; letter-spacing: 0.2em; color: #64748b; margin: 0 0 12px 0; font-weight: 700;">
            Key Person Dependencies
          </p>
          <p style="font-size: 16px; color: #334155; margin: 0; line-height: 1.6;">
            Key person dependencies were identified, indicating potential continuity risk during close periods.
          </p>
        </div>
        ` : ""}
      </div>

      <!-- Executive Summary -->
      <div style="page-break-after: auto; page-break-before: always; padding: 64px 56px 42px 56px; background-color: #ffffff;">
        <h2 style="font-size: 16px; font-weight: 700; text-transform: uppercase; letter-spacing: 0.16em; color: #64748b; margin: 0 0 24px 0;">
          Executive Summary
        </h2>
        <div style="background-color: #f8fafc; padding: 28px; border: 1px solid #dbe4f0; border-left: 5px solid #12d4cf; border-radius: 10px; page-break-inside: avoid;">
          <p style="font-size: 16px; color: #334155; margin: 0; line-height: 1.75;">
            ${narrative}
          </p>
        </div>
      </div>

      <!-- Advisory Next Steps -->
      <div style="page-break-after: auto; padding: 20px 56px 52px 56px; background-color: #ffffff;">
        <h2 style="font-size: 16px; font-weight: 700; text-transform: uppercase; letter-spacing: 0.16em; color: #64748b; margin: 0 0 24px 0;">
          What Organisations Typically Do Next
        </h2>
        <div style="space-y: 20px;">
          <p style="font-size: 16px; color: #334155; margin: 0 0 16px 0; line-height: 1.72;">
            Organisations with sustained reliance on manual journal orchestration typically focus on reducing operational dependency rather than eliminating judgment.
          </p>
          <p style="font-size: 16px; color: #334155; margin: 0 0 16px 0; line-height: 1.72;">
            This often involves standardising recurring journals, embedding controls earlier in the close process, and removing spreadsheet-driven handoffs that create rework, approval friction, and evidence gaps.
          </p>
          <p style="font-size: 16px; color: #334155; margin: 0; line-height: 1.72;">
            Redwood supports this transition by automating the execution, approval, and evidencing of recurring journals directly within the ERP, helping finance teams reduce close risk without disrupting accounting ownership.
          </p>
        </div>
      </div>

      <!-- Appendix -->
      <div style="page-break-after: auto; page-break-before: always; box-sizing: border-box; padding: 64px 56px 56px 56px; border-top: 1px solid rgba(15,23,42,0.12); background-color: #ffffff;">
        <h2 style="font-size: 16px; font-weight: 700; text-transform: uppercase; letter-spacing: 0.16em; color: #64748b; margin: 0 0 26px 0;">
          Appendix
        </h2>
        
        <h3 style="font-size: 18px; font-weight: 700; color: #0f172a; margin: 0 0 18px 0;">
          Exposure Band Definitions
        </h3>
        <div>
          ${Object.entries(bandDefinitions)
            .map(
              ([band, definition]) => `
            <p style="font-size: 15px; margin: 0 0 13px 0; color: #334155; line-height: 1.65;">
              <strong>${band}:</strong> ${definition}
            </p>
          `
            )
            .join("")}
        </div>

        <h3 style="font-size: 18px; font-weight: 700; color: #0f172a; margin: 28px 0 16px 0;">
          About This Assessment
        </h3>
        <p style="font-size: 15px; color: #334155; line-height: 1.72; margin: 0;">
          The Journal Automation Exposure Index measures how exposed an organisation's close process is to manual journal orchestration reliance. It evaluates seven dimensions: journal timing, journal drivers, spreadsheet dependence, approval friction, audit evidence handling, key person dependencies, and downstream rework frequency. The assessment produces an exposure band and narrative to guide process improvement prioritisation.
        </p>
      </div>

    </div>
  `;

  const element = document.createElement("div");
  element.innerHTML = htmlContent;
  element.style.width = "800px";
  element.style.padding = "0";
  element.style.margin = "0";

  const opt = {
    margin: [0, 0, 0, 0] as [number, number, number, number],
    filename: `Journal-Automation-Exposure-Index-${exposureBand}-${exposureScore}.pdf`,
    image: { type: "jpeg" as const, quality: 0.98 },
    html2canvas: { scale: 2, useCORS: true, logging: false, allowTaint: true, backgroundColor: "#ffffff" },
    jsPDF: { unit: "mm", format: "a4", orientation: "portrait" as const },
    pagebreak: { mode: ["css", "legacy"] }
  };

  html2pdf().set(opt).from(element).save();
}
