import { z } from "zod";

export const leadCaptureSchema = z.object({
  email: z.string().email("Please enter a valid email address"),
  company: z.string().optional(),
});

export type LeadCapture = z.infer<typeof leadCaptureSchema>;

export const exposureBands = ["Low", "Moderate", "Elevated", "High", "Critical"] as const;
export type ExposureBand = typeof exposureBands[number];

export const anchorTimingBuckets = ["early", "mid", "late"] as const;
export type AnchorTimingBucket = typeof anchorTimingBuckets[number];

export interface DiagnosticResult {
  id?: number;
  createdAt?: string;
  email?: string;
  company?: string;
  exposureScore: number;
  exposureBand: ExposureBand;
  primaryDriver: string;
  anchorTimingBucket: AnchorTimingBucket;
  dependencyFlag: boolean;
  answers?: QuestionAnswers;
  responseSummary?: string;
}

export const questionAnswersSchema = z.record(
  z.string(),
  z.number().int().min(0).max(4)
);

export const insertResultSchema = z.object({
  email: z.string().optional(),
  company: z.string().optional(),
  exposureScore: z.number().min(0).max(100),
  exposureBand: z.enum(exposureBands),
  primaryDriver: z.string(),
  anchorTimingBucket: z.enum(anchorTimingBuckets),
  dependencyFlag: z.boolean(),
  answers: questionAnswersSchema,
});

export type InsertResult = z.infer<typeof insertResultSchema>;

export interface Question {
  id: string;
  category: string;
  text: string;
  options: string[];
  isTriangulation?: boolean;
}

