import {
  REPORT_PAYLOAD_MAX_LENGTH,
  REPORT_PAYLOAD_QUERY_PARAM,
  decodeReportPayload,
  encodeReportPayload,
  type ReportPayloadV1
} from "@shared/report-payload";

export function buildReportUrl(payload: ReportPayloadV1): string {
  const encoded = encodeReportPayload(payload);
  if (encoded.length > REPORT_PAYLOAD_MAX_LENGTH) {
    throw new Error("Encoded payload exceeds safe URL length.");
  }

  const searchParams = new URLSearchParams();
  searchParams.set(REPORT_PAYLOAD_QUERY_PARAM, encoded);
  return `/report?${searchParams.toString()}`;
}

export function parseReportPayloadFromLocation(search: string): ReportPayloadV1 {
  const params = new URLSearchParams(search);
  const encodedPayload = params.get(REPORT_PAYLOAD_QUERY_PARAM);
  if (!encodedPayload) {
    throw new Error("Missing report payload.");
  }

  return decodeReportPayload(encodedPayload);
}

