import { useState } from "react";
import { useLocation } from "wouter";
import { type QuestionAnswers, type DiagnosticResult } from "@shared/schema";
import { REPORT_PAYLOAD_VERSION } from "@shared/report-payload";
import { calculateExposureScore } from "@/lib/scoring";
import { buildReportUrl } from "@/lib/reportPayload";
import { useParentCommunication } from "@/hooks/useParentCommunication";
import { IntroductionScreen } from "@/components/Introduction";
import { QuestionFlowScreen } from "@/components/QuestionFlow";
import { Loader2 } from "lucide-react";

type DiagnosticStep = "intro" | "questions" | "submitting";

export default function Diagnostic() {
  const [, navigate] = useLocation();
  const { isEmbedded, notifyComplete, notifyNavigate } = useParentCommunication();
  const [step, setStep] = useState<DiagnosticStep>("intro");

  const handleStartDiagnostic = () => {
    setStep("questions");
  };

  const handleQuestionsComplete = (answers: QuestionAnswers) => {
    const scores = calculateExposureScore(answers);

    const diagnosticResult: DiagnosticResult = {
      ...scores,
      answers
    };

    setStep("submitting");

    const reportUrl = buildReportUrl({
      v: REPORT_PAYLOAD_VERSION,
      s: diagnosticResult.exposureScore,
      b: diagnosticResult.exposureBand,
      p: diagnosticResult.primaryDriver,
      t: diagnosticResult.anchorTimingBucket,
      d: diagnosticResult.dependencyFlag ? 1 : 0,
      a: answers,
      ts: new Date().toISOString()
    });

    if (isEmbedded) {
      const reportBaseUrl =
        new URLSearchParams(window.location.search).get("reportBaseUrl") || "/report";
      const reportDestination =
        reportBaseUrl.includes("?")
          ? `${reportBaseUrl}&payload=${new URL(reportUrl, window.location.origin).searchParams.get("payload")}`
          : `${reportBaseUrl}?payload=${new URL(reportUrl, window.location.origin).searchParams.get("payload")}`;

      notifyComplete({
        payload: {
          v: REPORT_PAYLOAD_VERSION,
          s: diagnosticResult.exposureScore,
          b: diagnosticResult.exposureBand,
          p: diagnosticResult.primaryDriver,
          t: diagnosticResult.anchorTimingBucket,
          d: diagnosticResult.dependencyFlag ? 1 : 0,
          a: answers,
          ts: new Date().toISOString()
        },
        redirectUrl: reportDestination
      });
      return;
    }

    notifyNavigate(reportUrl);
    navigate(reportUrl);
  };

  switch (step) {
    case "intro":
      return <IntroductionScreen onStart={handleStartDiagnostic} />;
    case "questions":
      return <QuestionFlowScreen onComplete={handleQuestionsComplete} />;
    case "submitting":
      return (
        <div className="min-h-screen app-gradient-bg flex items-center justify-center px-4">
          <div className="text-center">
            <Loader2 className="w-8 h-8 animate-spin text-primary mx-auto mb-4" />
            <p className="text-muted-foreground" data-testid="text-loading">
              Preparing your report...
            </p>
          </div>
        </div>
      );
    default:
      return null;
  }
}
