import { useState } from "react";
import { type ExposureBand, type AnchorTimingBucket, questions, type QuestionAnswers } from "@shared/schema";
import { getExposureSummary, getExposureNarrative, getDriverInterpretation } from "@/lib/scoring";
import { generateResultsPDF } from "@/lib/pdf-generator";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Download, ChevronDown, ChevronUp, TrendingUp, Clock } from "lucide-react";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible";

interface ResultsProps {
  exposureBand: ExposureBand;
  exposureScore: number;
  primaryDriver: string;
  dependencyFlag: boolean;
  anchorTimingBucket: AnchorTimingBucket;
  answers?: QuestionAnswers;
}

const bandAccentColors: Record<ExposureBand, string> = {
  Low: "bg-primary",
  Moderate: "bg-primary",
  Elevated: "bg-primary",
  High: "bg-primary",
  Critical: "bg-primary"
};

export function ResultsScreen({ 
  exposureBand, 
  exposureScore,
  primaryDriver, 
  dependencyFlag,
  anchorTimingBucket,
  answers
}: ResultsProps) {
  const summary = getExposureSummary(exposureBand);
  const narrative = getExposureNarrative(exposureBand, primaryDriver, dependencyFlag, anchorTimingBucket);
  const driverInterpretation = getDriverInterpretation(primaryDriver);
  const [detailsOpen, setDetailsOpen] = useState(false);

  const handleDownloadPDF = () => {
    generateResultsPDF(
      exposureBand,
      exposureScore,
      summary,
      primaryDriver,
      driverInterpretation,
      anchorTimingBucket,
      dependencyFlag,
      narrative
    );
  };

  return (
    <div className="min-h-screen app-gradient-bg flex items-center justify-center px-4 py-8 sm:py-12">
      <div className="w-full max-w-2xl">
        <div className="text-center mb-10 sm:mb-14">
          <p className="text-xs uppercase tracking-[0.2em] text-muted-foreground mb-6 sm:mb-8 font-medium">
            Assessment Complete
          </p>
          
          {/* Framing box for band and score */}
          <div className="mb-10 sm:mb-12 p-6 sm:p-8 rounded-lg border border-primary/20 bg-primary/5">
            <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground mb-3 sm:mb-4 font-medium">
              Exposure Band
            </p>
            <h1 
              className="text-5xl sm:text-6xl md:text-7xl font-bold text-foreground mb-6"
              data-testid="text-band-display"
            >
              {exposureBand}
            </h1>
            
            <div className="flex items-center justify-center gap-2 mb-6 sm:mb-8">
              <span className="text-3xl sm:text-4xl font-semibold text-foreground" data-testid="text-exposure-score">
                {exposureScore}
              </span>
              <span className="text-base sm:text-lg text-muted-foreground">/100</span>
            </div>
            <p className="text-xs text-muted-foreground tracking-wide mb-3 sm:mb-4">
              Exposure Index Score
            </p>
            <p className="text-xs text-muted-foreground leading-relaxed">
              Scores in this range typically indicate sustained reliance on manual orchestration rather than isolated exceptions.
            </p>
          </div>

          <div className={`h-1 w-16 mx-auto rounded-full ${bandAccentColors[exposureBand]} mb-10 sm:mb-12`} />
          
          <p 
            className="text-sm sm:text-base text-foreground leading-relaxed max-w-xl mx-auto"
            data-testid="text-summary"
          >
            {summary}
          </p>
        </div>

        {/* Primary Driver and Journal Timing as icon cards */}
        <div className="mb-10 sm:mb-12">
          <p className="text-xs uppercase tracking-[0.2em] text-muted-foreground mb-6 sm:mb-7 font-medium">
            Key Assessment Factors
          </p>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 sm:gap-5 mb-10 sm:mb-12">
            {/* Primary Driver Card */}
            <div className="p-4 sm:p-5 rounded-lg border border-border bg-card/30 hover-elevate">
              <div className="flex items-start gap-3 sm:gap-4 mb-3">
                <div className="flex-shrink-0 mt-0.5">
                  <TrendingUp className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                </div>
                <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground font-medium">
                  Primary Contributing Factor
                </p>
              </div>
              <p 
                className="text-foreground font-medium text-base sm:text-lg ml-8 sm:ml-10 mb-3"
                data-testid="text-primary-driver"
              >
                {primaryDriver}
              </p>
              {driverInterpretation && (
                <p className="text-xs sm:text-sm text-muted-foreground leading-relaxed ml-8 sm:ml-10">
                  {driverInterpretation}
                </p>
              )}
            </div>

            {/* Journal Timing Card */}
            <div className="p-4 sm:p-5 rounded-lg border border-border bg-card/30 hover-elevate">
              <div className="flex items-start gap-3 sm:gap-4 mb-3">
                <div className="flex-shrink-0 mt-0.5">
                  <Clock className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                </div>
                <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground font-medium">
                  Journal Timing
                </p>
              </div>
              <p className="text-foreground font-medium text-base sm:text-lg capitalize ml-8 sm:ml-10">
                {anchorTimingBucket}-cycle finalization
              </p>
            </div>
          </div>

          {/* Key Person Dependency Section */}
          {dependencyFlag && (
            <div className="p-5 sm:p-6 rounded-lg border border-primary/20 bg-primary/5">
              <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground font-medium mb-2">
                Key Person Dependencies
              </p>
              <p className="text-sm text-foreground leading-relaxed">
                Key person dependencies were identified, indicating potential continuity risk during close periods.
              </p>
            </div>
          )}
        </div>

        {/* Executive Summary as framing box */}
        <div className="mb-10 sm:mb-12 p-5 sm:p-6 rounded-lg border border-primary/20 bg-primary/5">
          <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground font-medium mb-3 sm:mb-4">
            Executive Summary
          </p>
          <p 
            className="text-sm sm:text-base text-foreground leading-relaxed"
            data-testid="text-narrative"
          >
            {narrative}
          </p>
        </div>

        {/* Advisory Next Steps Section */}
        <div className="mb-10 sm:mb-12 border-t border-border pt-10 sm:pt-12">
          <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground font-medium mb-6 sm:mb-7">
            What organisations typically do next
          </p>
          <div className="space-y-4 text-sm sm:text-base text-muted-foreground leading-relaxed mb-8 sm:mb-10">
            <p>
              Organisations with sustained reliance on manual journal orchestration typically focus on reducing operational dependency rather than eliminating judgment.
            </p>
            <p>
              This often involves standardising recurring journals, embedding controls earlier in the close process, and removing spreadsheet-driven handoffs that create rework, approval friction, and evidence gaps.
            </p>
            <p>
              Redwood supports this transition by automating the execution, approval, and evidencing of recurring journals directly within the ERP, helping finance teams reduce close risk without disrupting accounting ownership.
            </p>
          </div>

          {/* Advisory CTA */}
          <div className="flex flex-col items-center gap-3">
            <Button
              variant="outline"
              onClick={handleDownloadPDF}
              data-testid="button-download-pdf"
            >
              <Download className="w-4 h-4 mr-2" />
              Download PDF
            </Button>
          </div>
        </div>

        {answers && (
          <Collapsible open={detailsOpen} onOpenChange={setDetailsOpen}>
            <Card className="border-card-border mb-6">
              <CollapsibleTrigger asChild>
                <button 
                  className="w-full px-4 sm:px-6 py-3 sm:py-4 flex items-center justify-between text-left hover-elevate"
                  data-testid="button-toggle-details"
                >
                  <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground">
                    View your responses
                  </p>
                  {detailsOpen ? (
                    <ChevronUp className="w-5 h-5 text-muted-foreground flex-shrink-0 ml-2" />
                  ) : (
                    <ChevronDown className="w-5 h-5 text-muted-foreground flex-shrink-0 ml-2" />
                  )}
                </button>
              </CollapsibleTrigger>
              <CollapsibleContent>
                <div className="px-4 sm:px-6 pb-6 border-t border-border pt-6">
                  <div className="space-y-6">
                    {questions.map((question, index) => {
                      const answerIndex = answers[question.id] ?? 0;
                      const selectedOption = question.options[answerIndex];
                      
                      return (
                        <div 
                          key={question.id} 
                          className="pb-6 border-b border-border last:border-0 last:pb-0"
                          data-testid={`detail-question-${question.id}`}
                        >
                          <p className="text-xs uppercase tracking-[0.15em] text-muted-foreground mb-2">
                            {index + 1}. {question.category}
                          </p>
                          <p className="text-sm text-foreground font-medium mb-3">
                            {question.text}
                          </p>
                          <p className="text-sm text-muted-foreground">
                            {selectedOption}
                          </p>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </CollapsibleContent>
            </Card>
          </Collapsible>
        )}

        <p className="text-center text-xs text-muted-foreground mt-8 sm:mt-10">
          Journal Automation Exposure Index v1.0
        </p>
      </div>
    </div>
  );
}
