import { Button } from "@/components/ui/button";
import { Users, Clock, CheckCircle } from "lucide-react";

interface IntroductionProps {
  onStart: () => void;
}

export function IntroductionScreen({ onStart }: IntroductionProps) {
  return (
    <div className="min-h-screen app-gradient-bg flex items-center justify-center px-4 py-8 sm:py-12">
      <div className="w-full max-w-3xl">
        {/* Pre-headline and main headline */}
        <div className="mb-6 sm:mb-8">
          <p className="text-xs uppercase tracking-[0.2em] text-muted-foreground mb-4 sm:mb-5 font-medium">
            Control Diagnostic
          </p>

          <h1 
            className="text-4xl sm:text-5xl md:text-6xl font-bold tracking-tight text-foreground mb-4"
            data-testid="text-intro-title"
          >
            Journal Automation Exposure Index
          </h1>

          {/* Teal accent line */}
          <div className="h-1 w-16 bg-primary rounded-full" />
        </div>

        {/* Core framing statement */}
        <div className="mb-10 sm:mb-12 p-6 sm:p-8 rounded-lg border border-primary/20 bg-primary/5">
          <p className="text-xl sm:text-2xl md:text-3xl font-semibold leading-relaxed text-foreground text-center">
            Manual journals are not inherently risky.<br />
            <span className="text-primary">The way they are prepared, approved,<br />
            and supported can be.</span>
          </p>
        </div>

        {/* Explanatory paragraph */}
        <p className="text-foreground leading-relaxed text-base sm:text-lg mb-10 sm:mb-12">
          This diagnostic shows how manual journal processes create control and close risk, based on how your close actually runs.
        </p>

        {/* What This Reveals section */}
        <div className="mb-10 sm:mb-12">
          <p className="text-xs uppercase tracking-[0.2em] text-muted-foreground mb-6 sm:mb-7 font-medium">
            What This Reveals
          </p>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 sm:gap-5">
            {/* Item 1 */}
            <div className="p-4 sm:p-5 rounded-lg border border-border bg-card/30 hover-elevate">
              <div className="flex items-start gap-3 sm:gap-4">
                <div className="flex-shrink-0 mt-0.5">
                  <Users className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                </div>
                <p className="text-foreground text-sm sm:text-base font-medium leading-relaxed">
                  Dependency on people and manual steps
                </p>
              </div>
            </div>

            {/* Item 2 */}
            <div className="p-4 sm:p-5 rounded-lg border border-border bg-card/30 hover-elevate">
              <div className="flex items-start gap-3 sm:gap-4">
                <div className="flex-shrink-0 mt-0.5">
                  <Clock className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                </div>
                <p className="text-foreground text-sm sm:text-base font-medium leading-relaxed">
                  Timing and approval risk during close
                </p>
              </div>
            </div>

            {/* Item 3 */}
            <div className="p-4 sm:p-5 rounded-lg border border-border bg-card/30 hover-elevate">
              <div className="flex items-start gap-3 sm:gap-4">
                <div className="flex-shrink-0 mt-0.5">
                  <CheckCircle className="w-5 h-5 sm:w-6 sm:h-6 text-primary" />
                </div>
                <p className="text-foreground text-sm sm:text-base font-medium leading-relaxed">
                  Audit evidence and control burden
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Outcome statement */}
        <div className="mb-10 sm:mb-12 p-5 sm:p-6 rounded-lg border border-primary/30 bg-primary/10">
          <p className="text-foreground text-base sm:text-lg leading-relaxed text-center">
            You'll receive a clear exposure classification<br />
            and an executive-ready summary.
          </p>
        </div>

        {/* CTA section */}
        <div className="border-t border-border pt-6 sm:pt-8 text-center">
          <p className="text-muted-foreground text-xs sm:text-sm mb-6 sm:mb-8 font-medium">
            Completion time: under 5 minutes
          </p>

          <Button 
            onClick={onStart}
            size="lg"
            className="px-8 sm:px-12 text-base sm:text-lg font-semibold"
            data-testid="button-begin"
          >
            View journal exposure
          </Button>
        </div>
      </div>
    </div>
  );
}
