import { z } from "zod";
import { anchorTimingBuckets, exposureBands, type AnchorTimingBucket, type ExposureBand, type QuestionAnswers } from "./schema";

export const REPORT_PAYLOAD_VERSION = 1 as const;
export const REPORT_PAYLOAD_QUERY_PARAM = "payload";
export const REPORT_PAYLOAD_MAX_LENGTH = 3500;

const reportPayloadSchema = z.object({
  v: z.literal(REPORT_PAYLOAD_VERSION),
  s: z.number().int().min(0).max(100),
  b: z.enum(exposureBands),
  p: z.string().min(1),
  t: z.enum(anchorTimingBuckets),
  d: z.union([z.literal(0), z.literal(1)]),
  a: z.record(z.string(), z.number().int().min(0).max(4)),
  ts: z.string().optional()
});

export interface ReportPayloadV1 {
  v: typeof REPORT_PAYLOAD_VERSION;
  s: number;
  b: ExposureBand;
  p: string;
  t: AnchorTimingBucket;
  d: 0 | 1;
  a: QuestionAnswers;
  ts?: string;
}

export function encodeReportPayload(payload: ReportPayloadV1): string {
  return encodeURIComponent(JSON.stringify(payload));
}

export function decodeReportPayload(input: string): ReportPayloadV1 {
  const decoded = decodeURIComponent(input);
  const parsed = JSON.parse(decoded);
  return reportPayloadSchema.parse(parsed);
}

