import { questions, type QuestionAnswers, type ExposureBand, type AnchorTimingBucket } from "@shared/schema";

const driverLabels: Record<string, string> = {
  anchor_timing: "Journal timing and approval patterns",
  journal_drivers: "Manual journal drivers and dependencies",
  spreadsheet_dependence: "Spreadsheet reliance in journal processing",
  approval_friction: "Approval workflow friction",
  audit_evidence: "Audit evidence burden",
  dependency_risk: "Key person dependency risk",
  rework_signal: "Downstream rework frequency"
};

const driverInterpretations: Record<string, string> = {
  "Journal timing and approval patterns": "Late or compressed journal timing increases close volatility and reduces flexibility.",
  "Manual journal drivers and dependencies": "Recurring operational journals signal underlying process gaps rather than isolated accounting judgement.",
  "Spreadsheet reliance in journal processing": "Spreadsheet-driven journals introduce fragility, rework, and version control risk.",
  "Approval workflow friction": "Approval delays create bottlenecks that amplify close pressure and rework.",
  "Audit evidence burden": "Manual evidence collection increases audit effort and late-stage adjustments.",
  "Key person dependency risk": "Reliance on specific individuals introduces continuity risk during close.",
  "Downstream rework frequency": "Rework after posting signals instability in journal execution."
};

export function getDriverInterpretation(driverLabel: string): string {
  return driverInterpretations[driverLabel] || "";
}

export function calculateExposureScore(answers: QuestionAnswers): {
  exposureScore: number;
  exposureBand: ExposureBand;
  primaryDriver: string;
  anchorTimingBucket: AnchorTimingBucket;
  dependencyFlag: boolean;
} {
  let weightedSum = 0;
  let maxWeightedSum = 0;
  let highestWeightedScore = 0;
  let primaryDriverId = "anchor_timing";
  let triangulationFlags = 0;

  questions.forEach((question) => {
    const answerIndex = answers[question.id] ?? 0;
    const rawScore = answerIndex;
    const weight = question.isTriangulation ? 0.75 : 1;
    const weightedScore = rawScore * weight;

    weightedSum += weightedScore;
    maxWeightedSum += 4 * weight;

    if (weightedScore > highestWeightedScore) {
      highestWeightedScore = weightedScore;
      primaryDriverId = question.id;
    }

    if (question.isTriangulation && answerIndex >= 3) {
      triangulationFlags++;
    }
  });

  const anchorAnswer = answers["anchor_timing"] ?? 0;
  let anchorMultiplier = 1.0;
  let anchorTimingBucket: AnchorTimingBucket = "mid";

  if (anchorAnswer <= 1) {
    anchorMultiplier = 0.9;
    anchorTimingBucket = "early";
  } else if (anchorAnswer === 2) {
    anchorMultiplier = 1.0;
    anchorTimingBucket = "mid";
  } else {
    anchorMultiplier = 1.2;
    anchorTimingBucket = "late";
  }

  let normalizedScore = (weightedSum / maxWeightedSum) * 100;
  normalizedScore *= anchorMultiplier;
  normalizedScore = Math.min(100, Math.max(0, normalizedScore));

  const dependencyFlag = triangulationFlags >= 1;

  let exposureBand: ExposureBand;
  let bandThresholds = [20, 40, 60, 80];

  if (triangulationFlags >= 2) {
    bandThresholds = bandThresholds.map(t => t - 10);
  }

  if (normalizedScore < bandThresholds[0]) {
    exposureBand = "Low";
  } else if (normalizedScore < bandThresholds[1]) {
    exposureBand = "Moderate";
  } else if (normalizedScore < bandThresholds[2]) {
    exposureBand = "Elevated";
  } else if (normalizedScore < bandThresholds[3]) {
    exposureBand = "High";
  } else {
    exposureBand = "Critical";
  }

  return {
    exposureScore: Math.round(normalizedScore),
    exposureBand,
    primaryDriver: driverLabels[primaryDriverId] || "Manual journal orchestration",
    anchorTimingBucket,
    dependencyFlag
  };
}

export function getExposureSummary(band: ExposureBand): string {
  const summaries: Record<ExposureBand, string> = {
    Low: "At this exposure level, your close process shows limited exposure to manual journal risk.",
    Moderate: "At this exposure level, manual journals introduce some dependency during close, but exposure remains manageable.",
    Elevated: "At this exposure level, manual journal processes represent a structural source of close risk.",
    High: "At this exposure level, manual journals create material dependency and volatility during close.",
    Critical: "At this exposure level, the close is highly dependent on manual journal execution and intervention."
  };
  return summaries[band];
}

export function getExposureNarrative(
  band: ExposureBand,
  primaryDriver: string,
  dependencyFlag: boolean,
  anchorBucket: AnchorTimingBucket
): string {
  const timingContext = {
    early: "Journal preparation is largely completed ahead of close activities.",
    mid: "Journals are typically finalised mid-close, creating moderate scheduling pressure.",
    late: "Journals are finalised late in the close cycle, increasing downstream timing risk and rework."
  };

  const bandContext: Record<ExposureBand, string> = {
    Low: "The current approach presents limited operational risk and does not materially constrain close execution.",
    Moderate: "While current processes are workable, reliance on manual journal execution increases sensitivity to timing and resourcing.",
    Elevated: "Reliance on manual journal execution creates tangible close risk that is difficult to mitigate without process change.",
    High: "Manual journal dependencies represent material close risk and increase the likelihood of late adjustments and rework.",
    Critical: "The degree of manual journal dependency represents an unacceptable level of close and control risk without intervention."
  };

  let narrative = `${timingContext[anchorBucket]} ${primaryDriver} is the primary driver of exposure. ${bandContext[band]}`;

  if (dependencyFlag) {
    narrative += " Key person dependencies were identified, indicating potential continuity risk during close periods.";
  }

  return narrative;
}
